import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import posed from 'react-pose';

import "../styles/header.css"
const Header = ({ siteTitle }) => (
  <header
    style={{
      //marginBottom: `1.45rem`
    }}
  >
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `0.45rem 0.0875rem`,
      }}
    >
      <Hoverh1 style={{ display: `inline-block`, margin: 0 }}>
        <Link to="/" style={{ color: `white`, textDecoration: `none`, }}>
          {siteTitle}
        </Link>
      </Hoverh1>
      <ul style={{ listStyle: `none`, float: `right`, marginBottom: '0px' }}>
        <Hoverli style={{ display: `inline-block`, marginBottom: '0px', marginRight: `1rem`, verticalAlign: 'middle' }}>
          <Link style={{ color: `white`, textDecoration: `none` }} to="/projects">Projects</Link>
        </Hoverli>
        <Hoverli style={{ display: `inline-block`, marginBottom: '0px', marginRight: `1rem`, verticalAlign: 'middle' }}>
          <Link style={{ color: `white`, textDecoration: `none`, }} to="/contact">Contact</Link>
        </Hoverli>
      </ul>
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

const Hoverh1 = posed.h1({
  hoverable: true,
    init: {
      //boxShadow: "0px 0px 0px rgba(0,0,0,0)",
      backgroundColor: '#426aa8',
    },
    hover: {
      backgroundColor: '#325385',
    }
})

const Hoverli = posed.li({
  hoverable: true,
    init: {
      //boxShadow: "0px 0px 0px rgba(0,0,0,0)",
      backgroundColor: '#426aa8',
    },
    hover: {
      backgroundColor: '#325385',
    }
})


export default Header
